.main {
    /* margin: auto; */
    display: flex;
    flex-direction: column;
    align-items: center;
}

.header {
    display: flex;
    justify-content: center;
    align-items: center;
    background: #20232a;
    height: 100px;
    width: 100%;
    margin-bottom: 50px;
}

.container {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    width: 600px;
    background: white;
    padding: 30px;
    border-radius: 15px;
}